//
//

export default {
  props: {
    icon: {
      type: String,
      required: true
    },
    size: {
      type: String,
      default: 'regular',
      validator (size) {
        return ['small', 'regular', 'large'].includes(size)
      }
    }
  },
  computed: {
    src () {
      return `/icons/${this.icon}.svg`
    }
  }
}
