import VueIntersect from 'vue-intersect'
import { NAV_LINKS, DEFAULT_ROUND } from '~/libs/defs'

const WAIT_UI_REFRESH = 50

export default {
  components: {
    VueIntersect
  },
  data () {
    return {
      menuOpened: false,
      searchOpened: false,
      query: '',

      isMobileSticky: false,
      isDesktopSticky: false
    }
  },
  computed: {
    navLinks () {
      return NAV_LINKS
    },
    round () {
      return this.$route.params.round || DEFAULT_ROUND
    }
  },
  watch: {
    $route () {
      this.menuOpened = false
    }
  },
  methods: {
    toggleMenu () {
      this.menuOpened = !this.menuOpened

      if (!this.menuOpened) {
        this.closeSearch()
      }
    },
    triggerSearch () {
      if (!this.searchOpened) {
        this.searchOpened = true
        setTimeout(() => {
          [
            this.$refs.desktopSearch,
            this.$refs.mobileSearch
          ]
            .some((dom) => {
              if (dom.offsetHeight) {
                dom.focus()
                return true
              }
              return false
            })
        }, WAIT_UI_REFRESH)
      } else {
        // TODO: 議員、選區 search
        if (this.query) {
          this.$router.push({
            name: 'round-interpellation',
            params: {
              round: this.$route.params.round || DEFAULT_ROUND
            },
            query: {
              query: this.query
            }
          })
        }
        this.closeSearch()
      }
    },
    closeSearch (e) {
      if (e && e.relatedTarget && e.relatedTarget.classList.contains('nav__searchCta')) {
        this.triggerSearch()
      } else {
        this.query = ''
        this.searchOpened = false
      }
    },
    enterMobileSticky () {
      this.isMobileSticky = true
    },
    leaveMobileSticky () {
      this.isMobileSticky = false
    },
    enterDesktopSticky () {
      this.isDesktopSticky = true
    },
    leaveDesktopSticky () {
      this.isDesktopSticky = false
    }
  }
}
