import misc from '~/content/meta/misc.json'

export default {
  computed: {
    about () {
      return misc['footer-about']
    },
    contact () {
      return misc['footer-contact']
    }
  },
  methods: {
    isExt (about) {
      return about.url.match(/^https?:\/\//)
    }
  }
}
